import React, {Component} from 'react'
import Top from '../sideComponent/top.js'
import Header from '../sideComponent/header.js'
import Nav from '../sideComponent/nav.js'

import Footer from '../sideComponent/footer.js'

import {Link} from 'react-router-dom'

export default class NewsSingle extends Component{
	constructor(){
		super()
	}

	componentDidMount(){
		console.log(this.props.location);
	}

	render(){
		let detail = this.props.location.state.content.detail
		return(
			<div className="newsSingle">

				<Top/>
		    	<Header/>
		    	<Nav/>

		    	<div className="newsSingleMain container">

		    		<h2> {this.props.location.state.content.title}</h2>

					<div>
						<img src={this.props.location.state.content.img}/>
					</div>

					<span> <i className="fa fa-user"></i> {this.props.location.state.content.author}</span>
					<span> <i className="fa fa-calender"></i> {this.props.location.state.content.published}</span>

					<p style={{width:'80%',marginTop:'30px'}}>
						{detail}
					</p>


					<div className='back'> <Link to='/'> &lt; back </Link></div>
		    	</div> 



		    
		    	<Footer/>

			</div>

		);
	}
}